<template>
 <div id="forgotStep">
  <head-top title="Forgot Password" goBack="true"></head-top>
  <div class="resendEmail-Img">
    <img src="../../assets/login/click-emailicon@3x.png" alt="">
  </div>
  <div class="resendTip">
    <p>We have sent a confirmation email to <br/>
    <span>{{this.$route.params.email}}</span>, please click on the link in the email to complete the account activation </p>
  </div>
  <div class="finish" @click="$router.push({'path': '/login'})">
    Finished
  </div>
 </div>
</template>
<script>
import headTop from '../../components/common/head'
export default {
  data () {
    return {
      emailActive: false, // 是否已输入邮箱
      emailVal: null // 邮箱账号
    }
  },
  components: {
    headTop
  },
  mounted () {
    console.log(this.$route.params.email)
  },
  methods: {
    emailVolidate () {
      if (this.emailVal) {
        this.emailActive = true
      } else {
        this.emailActive = false
      }
    },
    foundEmail () {
      if (this.emailActive) {
        this.$router.push({path: 'forgotStep'})
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../styles/mixin.less";
#forgotStep{
  .fs;
  z-index: 999;
  background: white;
  .resendEmail-Img{
    .wh(243px, 218px);
    margin:  0 auto;
    margin-top: 48px;
    img{
      .wh(100%,100%)
    }
  }
  .resendTip{
    line-height: 52px;
    margin-top: 35px;
    .wh(100%, auto);
    padding: 0 30px;
    p{
      color: #666666;
      font-size: 22px;
      text-align: left;
    }
    span{
      font-weight: 900;
      color: #585858;
    }
  }
  .finish{
    .wh-l(520px, 102px);
    border-radius: 10px;
    margin: 0 auto;
    margin-top: 380px;
    background: #13af68;
  }
}
</style>



